<template>
  <div>
  <div v-if="!error">
    <div v-if="$route.query.id">
      <div v-if="waitingInfo && waitingRTPI">
        <load-animation/>
      </div><!-- end if upcommingbusses -->
      <div v-else class='busses'>
        <div class='stop-info'>
          <div class='three columns stop-name'>
            {{currentStopInfo.fullname}}
          </div>
          <div class='two columns'>
            {{currentStopInfo.stopid}}
          </div>
          <div>
              <button class='button-primary' v-on:click='getRealtimeInfo'><i class='fa fa-refresh'></i></button>
          </div>
        </div>
        <div class='twelve columns'>
          <table class='u-full-width'>
            <thead>
              <tr>
                <th>Route</th>
                <th>Destination</th>
                <th>Due</th>
              </tr>
            </thead>
            <tbody>
              <tr v-for='bus in upCommingBusses'>
                <td>{{bus.route}}</td>
                <td>{{bus.destination}}</td>
                <td>{{bus.duetime}} {{bus.duetime === 'Due' ? '' : 'mins'}}</td>
              </tr>
            </tbody>
          </table>
        </div>
      </div>
    </div><!-- end if $route.query.id -->
    <div v-else class='offset-by-three six columns'>
        <h4>Enter your stop number below</h4>
        <div class='offset-by-one eight columns'>
          <input placeholder='Stop number' v-model='stop' name='id' type=text/>
          <button class="button-primary"><router-link :to="{path: 'stop', query: {id: stop} }" class='button-primary'>Submit</router-link></button>
        </div>
    </div>
  </div> <!-- end if !error -->
  <div v-else>
    <h4>Error Happened</h4>
    <p>{{error_msg}}</p>
  </div>
</div>
</template>

<script>
import Loader from '@/components/Loader'
var XHTMLHttpRequestPromise = require('xhr-promise')

export default {
  name: 'view-stop',
  data () {
    return {
      upCommingBusses: [],
      stop: '',
      currentStopInfo: {},
      waitingInfo: true,
      waitingRTPI: true,
      error: false,
      error_msg: ''
    }
  },
  created: function () {
    if (this.upCommingBusses.length < 1) {
      this.getRealtimeInfo()
    }
    this.getStopInfo()
  },
  watch: {
    '$route' (to, from) {
      if (to.query.id !== from.query.id) {
        this.getRealtimeInfo()
        this.getStopInfo()
      }
    }
  },
  methods: {
    getRealtimeInfo: function () {
      if (this.$route.query.id !== undefined) {
        this.waitingInfo = true
        var xhr = new XHTMLHttpRequestPromise()
        xhr.send({
          method: 'GET',
          url: 'https://prettybus-cache.herokuapp.com/rtpi/stop/' + this.$route.query.id + '/'
        }).then(function (result) {
          this.waitingInfo = false
          if (result.status === 200) {
            var rB = JSON.parse(result.responseText)
            this.upCommingBusses = rB.results
          } else {
            this.errorHappened(result.responseText.message)
          }
        }.bind(this))
      }
    },
    getStopInfo: function () {
      if (this.$route.query.id !== undefined) {
        var xhr = new XHTMLHttpRequestPromise()
        this.waitingRTPI = true
        xhr.send({
          method: 'GET',
          url: 'https://prettybus-cache.herokuapp.com/stop/' + this.$route.query.id + '/'
        }).then(function (result) {
          this.waitingRTPI = false
          if (result.status === 200) {
            var rB = JSON.parse(result.responseText)
            this.currentStopInfo = rB.results[0]
          } else {
            this.errorHappened(result.responseText.message)
          }
        }.bind(this))
      }
    },
    errorHappened: function (errorMessage) {
      this.error = true
      this.error_msg = errorMessage
    }
  },
  components: { 'load-animation': Loader }
}
</script>

<style lang='scss'>
.stop-info{
  font-size:1.5em;
  font-weight:600;
}
table{
  border-collapse: collapse;
}
button > a{
  color: white;
  text-decoration:none;
  &:visited{
    color: white
  }
}

</style>



