<template>
  <div class="loading">
    <div class="rect1"></div>
    <div class="rect2"></div>
    <div class="rect3"></div>
    <div class="rect4"></div>
    <div class="rect5"></div>
    <span>Loading</span>
  </div>
</template>

<script>
export default {
  name: 'load-animation',
  data () {
    return {}
  }
}
</script>

<style lang='scss'>
  .loading{
    height: 45px;
    width:40px;
    margin: auto;
    color:#CDCDC0;

    div {
      display: inline-block;
      background-color:#333;
      width: 5px;
      height: 100%;
      margin-left:-1px;
      animation: 1.2s infinite 0s ease-in-out strech;
    }

    @for $i from 1 to 6 {
      .rect#{$i}{
        @if $i % 2 == 1{
          animation-delay:0.2s;
        }
        @else{
          animation-delay:-0.2s;
        }
      }
    }
    span{
      font-size:0.80em;
    }
  }

  @keyframes strech {
    0%, 40%, 100% {
      transform: scaleY(0.4);
    }
    20% {
      transform: scaleY(1);
    }
  }
</style>



