<template>
<div v-if="!error">
<div v-if="availableStops.length < 1">
  <load-animation/>
</div>
<div class='stops' v-else>
  <table class='u-full-width'>
    <thead>
      <tr>
        <th>Stop number</th>
        <th>Stop location</th>
        <th>Serving routes</th>
      </tr>
    </thead>
    <tbody>
      <tr v-for='stop in availableStops'>
        <td><router-link :to="{path: 'stop', query: {id: stop.stopid}}" >{{stop.stopid}}</router-link></td>
        <td><router-link :to="{path: 'stop', query: {id: stop.stopid}}" >{{stop.shortname}}</router-link></td>
        <td><span v-for='route in stop.operators[0].routes' class="routes">{{route}}</span></td>
      </tr>
    </tbody>
  </table>
</div>
</div>
<div v-else>
  <h4>Error Happened</h4>
  <p>{{error_msg}}</p>
</div>
</template>

<script>
// https://data.dublinked.ie/cgi-bin/rtpi/busstopinformation?operator=bac
import Loader from '@/components/Loader'
var XHTMLHttpRequestPromise = require('xhr-promise')
var xhr = new XHTMLHttpRequestPromise()
export default {
  name: 'list-stops',
  data () {
    return {
      availableStops: [],
      error: false,
      error_msg: ''
    }
  },
  created: function () {
    if (this.availableStops.length < 1) {
      xhr.send({
        method: 'GET',
        url: 'https://prettybus-cache.herokuapp.com/stop'
      }).then(function (result) {
        if (result.status === 200) {
          var resBody = JSON.parse(result.responseText)
          this.availableStops = resBody.results
        } else {
          this.error = true
          this.error_msg = result.responseText.message
        }
      }.bind(this))
    }
  },
  components: { 'load-animation': Loader }
}
</script>

<style lang='scss'>
span.routes{
  display:inline-block;
  padding-top: 3px;
  padding-bottom:3px;
  padding-right:4px;
  padding-left:4px;
  background:rgba(66, 183, 226, 0.74);
  color:white;
  margin-right:2px;
  margin-bottom:2px;
  border-radius:5px;
}

table{
  table-layout:fixed;
}
</style>



