<!-- main layout file -->
<template>
  <div id="app" v-on:resize='menu_visible = false'>
    <div class='container'>
      <div class='row'>
        <nav-bar :routeMap="routeMap"></nav-bar>
      </div>
      <div class='row'>
        <router-view></router-view>
      </div>
    </div>
  </div>
</template>

<script>
import NavBar from '@/components/Navbar'
export default {
  name: 'app',
  props: ['routeMap'],
  data () {
    return {
      menu_visible: false
    }
  },
  components: { 'nav-bar': NavBar }
}
</script>

<style lang='scss'>

@import './scss/skeleton';
@import './scss/utils';

body{
  padding:0px;
  margin:0px;
}
.app{
  width:100%;
}
</style>



